@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');



/*########## Variables ##########*/
:root, :root.light-mode {
    --bg-color-1: #f1fafe;                  /* Base BG      */
    --bg-color-2: #e0eaf6;                  /* On-hover     */
    --text-color-1: #0c3264;                /* Normal       */
    --text-color-2: #8b2781;                /* Headers      */
    --text-color-3: #1981eb;                /* On-hover     */
    --border-color: var(--text-color-2);    /* Border Color */
}
:root.dark-mode {
    --bg-color-1: #0c3264;
    --bg-color-2: #25507f;
    --text-color-1: #f1fafe;
    --text-color-2: #de88f0;
    --text-color-3: #99d7fb;
    --border-color: var(--text-color-2);
}



/*########## Base ##########*/
* {
    /* outline: 1px solid red; */
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Quicksand";
    color: var(--text-color-1);
}
body {
    background-color: var(--bg-color-1);
}



/*########## Navbar ##########*/
.nav-parent {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
}
.navbar {
    display: flex;
    background: var(--bg-color-1);
    align-items: center;
    max-width: 800px;
    height: 90px;
    margin: auto;
    padding: 20px;
    border-bottom: 1px solid var(--border-color);
}
.navbar > * {
    height: 98%;
    margin: 1%;
}

/* Logo */
.navbar .homeButton {
    width: 36%;
    display: flex;
    align-items: center;
}
.navbar .homeButton a {
    color: var(--text-color-2);
    text-decoration: none;
}

/* Links */
.navbar .links {
    width: 52%;
    display: flex;
    justify-content: end;
    align-items: center;
    margin-left: auto;
}
.navbar .links a {
    display: block;
    margin-left: 2%;
    text-decoration: none;
    padding: 1.5%;
    text-align: center;
}
.navbar .links a:hover {
    color: var(--text-color-3);
    background-color: var(--bg-color-2);
    border-radius: 8px;
}
.navbar .links .extra-close {
    display: none;
}

/* Theme Button */
.theme-btn-wrapper {
    width: 4%;
    display: flex;
    align-items: center;
    min-width: 30px;
    min-height: 30px;
}
.navbar .theme-btn {
    width: 100%;
    cursor: pointer;
    aspect-ratio: 1;
    background: none;
    border: none;
}
.light-btn, .dark-mode .dark-btn { display: none; }
.dark-btn, .dark-mode .light-btn { display: block; }
.theme-btn-wrapper svg { height: 100%; width: 100%; }
.theme-btn-wrapper svg * { fill: var(--text-color-1); }

/* Menu */
.navbar .menu-button { display: none; }
.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    border-radius: 5px;
    background-color: var(--text-color-1);
    margin: 6px 0;
    transition: 0.4s;
}
.menu-show .bar1 { transform: translate(0, 11px) rotate(-45deg); }
.menu-show .bar2 { opacity: 0; }
.menu-show .bar3 { transform: translate(0, -11px) rotate(45deg); }



/*########## Content ##########*/
.content {
    max-width: 800px;
    margin: 90px auto 40px;
    padding: 20px;
}
.page-header {
    padding-bottom: 10px;
    border-bottom: 1px solid var(--text-color-1);
    margin-bottom: 20px;
}
.no-image {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    color: white;
    background-color: grey;
}



/*########## Home ##########*/
.home .slide {
    display: none;
    width: 100%;
    height: auto;
    aspect-ratio: 16 / 9;
    overflow: hidden;
}
.home .slide > img {
    min-width: 100%;
    object-fit: cover;
}
.home .slide.active {
    display: block;
}
.home .slides-wrapper {
    border: 5px solid var(--border-color);
    border-radius: 10px;
    overflow: hidden;
    width: 96%;
    height: auto;
    position: relative;
    margin: 2%;
}
.home .prev-btn, .home .next-btn {
    position: absolute;
    top: 50%;
    cursor: pointer;
    width: auto;
    padding: 10px;
    transition: 0.3s ease;
    border-radius: 0 10px 10px 0;
    color: var(--border-color);
    background: var(--bg-color-1);
    opacity: 0.8;
}
.home .next-btn {
    right: 0;
    border-radius: 10px 0 0 10px;
}
.home .prev-btn:hover, .home .next-btn:hover {
    opacity: 1;
}
.home .slide-text {
    display: none;
    position: absolute;
    width: 100%;
    background: var(--bg-color-2);
    opacity: 0.8;
    bottom: 0;
    padding: 10px;
}
.home .slide.slide-visible {
    display: flex;
    justify-content: center;
    align-items: center;
}
.home .slide:hover .slide-text {
    display: block;
}




/*########## About Us ##########*/
.about-us {
    width: 100%;
}
.about-us .section-header {
    margin-bottom: 20px;
}
.about-us .officer-show {
    margin: 40px auto;
}
.about-us .officer-grid {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 500px;
}
.about-us .officer-tab {
    border: 2px solid var(--border-color);
    border-radius: 20px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    box-shadow: 5px 5px 5px 0 var(--bg-color-2);
}
.about-us .officer-tab .img-wrapper {
    margin: 5% auto;
    height: 30%;
    aspect-ratio: 1;
    border-radius: 10px;
    overflow: hidden;
}
.about-us .officer-tab img {
    display: block;
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.about-us .officer-tab .headers {
    height: 15%;
    text-align: center;
}
.about-us .officer-tab p {
    height: 50%;
    overflow-y: scroll
}
.about-us .faq {
    margin-bottom: 20px;
}
.about-us .faq h4 {
    margin-bottom: 10px;
}



/*########## Game Previews ##########*/
.schedule .event-list { margin-bottom: 30px; }
.schedule .event-list > h2 { margin-bottom: 10px; }
.schedule .event-item {
    border: 2px solid var(--border-color);
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 10px;
}
.schedule .event-item-header > h2 {
    color: var(--text-color-2);
}
.schedule .event-item-header {
    cursor: pointer;
    margin-bottom: 5px;
}
.schedule .event-item > p {
    display: none;
}
.schedule .event-item > p.content-show {
    display: block;
}



/*########## Resources ##########*/
.resources .contact-us {
    border: 5px solid var(--border-color);
    border-radius: 10px;
    padding: 10px;
    box-shadow: 3px 5px 5px 0 var(--bg-color-2);
    margin-bottom: 60px;
}
.resources .contact-us h2 { margin-bottom: 10px; }
.resources .contact-us ul { list-style-type: none; }
.resources .category-header {
    cursor: pointer;
    margin-bottom: 20px;
}
.resources .category-wrapper { display: none; }
.resources .category-visible { display: block; }
.resources .category {
    border: 2px solid var(--border-color);
    border-radius: 10px;
    padding: 10px;
    margin-bottom: 30px;
}
.resources .subcategory-header { margin-bottom: 15px; }
.resources .subcategory > p {margin-bottom: 15px; }
.resources .subcategory { margin-bottom: 20px; }
.resources .subcategory li {
    margin-left: 15px;
}




/*########## Game Previews ##########*/
.game-tab {
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    width: 100%;
    height: 120px;
    padding: 10px 16px;
    margin: 20px 0;
    border: 1px solid var(--border-color);
    border-bottom: 5px solid var(--border-color);
    border-radius: 10px;
}
.game-tab:hover {
    box-shadow: 3px 5px 5px 2px var(--bg-color-2);
    color: var(--text-color-1);
}
.game-tab .game-tab-text {
    display: flex;
    flex-direction: column;
    max-height: 100%;
}
.game-tab .game-tab-text h2 { color: var(--text-color-2); }
.game-tab .game-tab-text h4 { margin-bottom: 7px; }
.game-tab .game-tab-text p { overflow-y: scroll }
.game-tab .game-tab-img {
    height: 100%;
    width: auto;
    min-width: 100px;
    aspect-ratio: 1;
    border-radius: 10%;
    overflow: hidden;
}
.game-tab .game-tab-img * {
    height: 100%;
    width: 100%;
    object-fit: cover;
}



/*########## Game Page ##########*/
.game-page { width: 100%; }
.game-page .img-wrapper {
    margin: 20px 0;
    display: block;
    width: 100%;
}
.game-page .img-preview {
    height: 400px;
    width: 100%;
    background: var(--bg-color-2);
    border-radius: 15px;
    overflow: hidden;
}
.game-page .img-preview * {
    margin: auto;
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.game-page .img-gallery {
    margin-top: 5px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 5px;
    background: var(--bg-color-2);
    border-radius: 15px;
    overflow: hidden;
}
.game-page .img-gallery *{
    cursor: pointer;
    width: 100%;
    height: auto;
    aspect-ratio: 1;
    object-fit: cover;
}
.game-page .game-about { margin-bottom: 10px; }



/*########## Mobile Width ##########*/
@media (max-width: 650px){
    .navbar .menu-button {
        display: flex;
        margin-left: 5%;
        align-items: center;
    }
    .navbar .links { display: none; }
    .navbar .links.menu-show {
        display: block;
        position: fixed;
        top: 90px;
        left: 0;
        margin: 0;
        padding: 0;
        width: 100%;
        height: 100%;
    }
    .navbar .links.menu-show a {
        width: 100%;
        text-align: center;
        background: var(--bg-color-1);
        margin: 0;
        padding: 10px 0;
        font-size: 24px;
        border-bottom: 1px solid var(--border-color);
    }
    .navbar .links.menu-show a:hover {
        border-radius: 0;
        background: var(--bg-color-1);
        background-color: var(--bg-color-2);
    }
    .navbar .links.menu-show .extra-close {
        display: block;
        height: 100%;
    }
    .theme-btn-wrapper { margin-left: auto; }
    .navbar .theme-btn { border: none; }

    .about-us .officer-grid {
        grid-template-columns: 1fr;
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    }
}
